<template>
  <view class="">
    <view class="head">
      <text class="title">
        {{petInfo.name}}
      </text>
      <text class="tem-title">
        {{petInfo.plots.mu}} > {{petInfo.plots.ke}} > {{petInfo.plots.shu}}
      </text>
    </view>
    <view class="body">
      <!-- 轮播图 -->
      <view>
        <u-swiper :list="list" :effect3d="true" height="500"></u-swiper>
      </view>
      
      <!-- 基本信息 -->
      <view class="basic-info box">
        <u-divider  half-width="100">
          <view class="title2">
            <u-icon label="基本信息" size="40" name="info-circle"></u-icon>
          </view>
        </u-divider>
        <u-row class="row-margin">
          <u-col span="4">
            <view class="labe">名称：</view></u-col>
          <u-col span="8">
            <view class="text-info">{{petInfo.name}}</view>
          </u-col>
        </u-row>
        <u-row class="row-margin">
          <u-col span="4">
            <view class="labe">拉丁学名：</view></u-col>
          <u-col span="8">
            <view class="text-info ladi">{{petInfo.ladiName}}</view>
          </u-col>
        </u-row>
        <u-row class="row-margin">
          <u-col span="4">
            <view class="labe">目：</view></u-col>
          <u-col span="8">
            <view class="text-info">{{petInfo.plots.mu}}</view>
          </u-col>
        </u-row>
        <u-row class="row-margin">
          <u-col span="4">
            <view class="labe">科：</view></u-col>
          <u-col span="8">
            <view class="text-info">{{petInfo.plots.ke}}</view>
          </u-col>
        </u-row>
        <u-row class="row-margin">
          <u-col span="4">
            <view class="labe">属：</view></u-col>
          <u-col span="8">
            <view class="text-info">{{petInfo.plots.shu}}</view>
          </u-col>
        </u-row>
      </view>
      
      <!-- 间隔线 -->
      <view class="wall">
        <u-gap height="5" bg-color="#e7e7e7"></u-gap>
      </view>
      
      <!-- 分布地区 -->
      <view class="basic-info box">
        <u-divider  half-width="100">
          <view class="title2">
            <u-icon label="分布地区" size="40" name="info-circle"></u-icon>
          </view>
        </u-divider>
        <u-row class="row-margin">
          <!-- <u-col span="4">
            <view class="labe"></view>
            </u-col> -->
          <u-col span="10" offset="1">
            <view class="text-info">{{petInfo.distributionArea}}</view>
          </u-col>
        </u-row>
      </view>
      
      
      <!-- 返回顶部 -->
      <view class="wrap">
        <u-back-top :scroll-top="scrollTop"></u-back-top>
      </view>
    </view>
  </view>
  
  
</template>

<script>
  export default {
  	data() {
  		return {
        petInfo: {
          'name': '',
          'ladiName':'',
          'plots': {
            'mu': '',
            'ke': '',
            'shu': ''
          },
          distributionArea: ''
        },
        list: [{
            image: '',
            title: ''
          },
          {
            image: '',
            title: ''
          },
          {
            image: '',
            title: ''
          }
        ],
        scrollTop: 0
  		}
  	},
    onPageScroll(e) {
      this.scrollTop = e.scrollTop;
    },
  	onLoad(option) {
      // 获取害虫id
      console.log(option.id);
      
      uni.request({
        url: 'http://ocr.server.truedei.com/ocr/pest/selectPestsById?id=' + option.id,
        method:'GET',
        // data: ,
        success: res => {
          console.log(res.data.result);
          // 保存查询后的害虫数据
          var data = res.data.result;
          var imagesData = data.images.split("#####");
          //初始化图片
          for (var i = 0;i<imagesData.length;i++) {
            this.list[i].image = imagesData[i];
          }
          this.petInfo.name = data.pastName;
          this.petInfo.ladiName = data.latinCode;
          this.petInfo.plots.mu = data.item;
          this.petInfo.plots.ke = data.branch;
          this.petInfo.plots.shu = data.dependant;
          this.petInfo.distributionArea = data.distributionArea;
        },
        fail: err=>{
          reject(err);
        }
      });
      
    },
  	methods: {
  
  	}
  }
</script>

<style>
  .head text {
    display: block;
    text-align: center;
  }
  .head .title {
    margin: 28rpx 0;
    font-size: 48rpx;
    font-weight: 700;
  }
  .head .tem-title {
    font-size: 30rpx;
    margin: 10rpx 0 50rpx;
  }
  
  .title2 {
    display: block;
    text-align: center;
    margin: 28rpx 0;
    font-size: 32rpx;
    font-weight: 700;
  }
  .box {
    margin: 40rpx auto 5rpx;
    width: 90%;
    /* border: #aaaa7f 1rpx solid; */
    border-radius: 16rpx;
    padding-bottom: 1.5em;
  }
  
  .box .labe,
  .box .text-info{
    font-size: 30rpx;
  }
  .box .ladi{
    font-size: 24rpx;
  }
  
  .box .labe{
    text-align: center;
    font-weight: 700;
    color: #606266;
  }
  .box .text-info{
    text-align: left;
  }
  .row-margin {
    margin: 10rpx 0;
  }
  
  .wall {
    width: 90%;
    margin: 0 auto;
  }
</style>
